import React from "react";
import {
  Navbar,
  NavbarBrand,
  NavbarContent,
  NavbarItem,
  Link,
  Button,
} from "@nextui-org/react";
import ChartIcon from "../icons/ChartIcon";
import EmailIcon from "../icons/EmailIcon";
import TwitterIcon from "../icons/TwitterIcon";
import GithubIcon from "../icons/GithubIcon";
import LinkedInIcon from "../icons/LinkedInIcon";

export default function Footer() {
  return (
    <Navbar className="animate__animated animate__fadeInDown">
      <NavbarBrand as={Link} className="gap-2 text-white" href="/">
        <ChartIcon />
        <p className="font-bold text-2xl">TradeVault</p>
      </NavbarBrand>
      <NavbarContent justify="end">
        <NavbarItem>
          <Link className="text-white" href="mailto:anaismateusc@gmail.com">
            <EmailIcon />
          </Link>
        </NavbarItem>
        <NavbarItem>
          <Link className="text-white" href="https://github.com/anaiscmateus">
            <GithubIcon />
          </Link>
        </NavbarItem>
        <NavbarItem>
          <Link className="text-white" href="https://twitter.com/anaiscodes">
            <TwitterIcon />
          </Link>
        </NavbarItem>
        <NavbarItem>
          <Link
            className="text-white"
            href="https://www.linkedin.com/in/ana%C3%AFsmateus/"
          >
            <LinkedInIcon />
          </Link>
        </NavbarItem>
      </NavbarContent>
    </Navbar>
  );
}
