import { useState, useEffect } from "react";
import LikeIcon from "../icons/LikeIcon";
import DeleteIcon from "../icons/DeleteIcon";

export default function Posts({ refresh, setRefresh }) {
  const [posts, setPosts] = useState({ posts: [] });
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Standalone fetch posts function
  const fetchPosts = async () => {
    try {
      console.log(process.env.REACT_APP_API_URL)
      const response = await fetch(`/api/getPosts`);
      const data = await response.json();
      setPosts(data);
    } catch (error) {
      setError(error);
    } finally {
      setIsLoading(false);
      if (refresh) {
        setRefresh(false);
      }
    }
  };
  
  // Fetch posts on component mount
  useEffect(() => {
    fetchPosts();
  }, [refresh]);
  
  // handle likes
  const handleLike = async (postId) => {
    try {
      const response = await fetch(
        `/api/likePost/${postId}`,
        {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
        }
      );

      if (response.ok) {
        // Assuming getPosts is an object with a 'posts' array
        setPosts((currentPosts) => ({
          ...currentPosts,
          posts: currentPosts.posts.map((post) =>
            post._id === postId ? { ...post, likes: post.likes + 1 } : post
          ),
        }));        
      }
    } catch (error) {
      console.error("There was an error liking the post: ", error);
    }
  };

  // delete post
  const deletePost = async (postId) => {
    try {
      const response = await fetch(
        `/api/deletePost/${postId}`,
        {
          method: "DELETE",
          headers: { "Content-Type": "application/json" },
        }
      );

      if (response.ok) {
        // Update local state using setPosts from parent
        setPosts((currentPosts) => ({
          ...currentPosts,
          posts: currentPosts.posts.filter((post) => post._id !== postId),
        }));

        fetchPosts();
        
      } else {
        console.error("Failed to delete the post", await response.json());
      }
    } catch (error) {
      console.error("There was an error deleting the post: ", error);
    }
  };

  // Display when loading data
  if (isLoading) {
    return <p>Loading data...</p>;
  }

  // Display if error occurs when fetching data
  if (error) {
    return <p>Error fetching data: {error.message}</p>;
  }

  return (
    <>
      {posts.posts.map((post) => (
        <div key={post._id} className="m-2">
          <div className="flex items-center gap-1">
            <span>{post.firstName}</span>
            <span>{post.lastName}</span>
            <button
              className="text-white bg-blue-500 hover:bg-blue-300 rounded-full p-2"
              onClick={() => handleLike(post._id)}
            >
              <LikeIcon />
            </button>
            <button
              className="text-white bg-red-500 hover:bg-red-300 rounded-full p-2"
              onClick={() => deletePost(post._id)}
            >
              <DeleteIcon />
            </button>
          </div>
          <img
            className="my-2"
            src={post.imageUrl}
            alt="upload-img"
            width={200}
          />
          <div className="flex gap-2">
            <span>Likes:</span>
            <span>{post.likes}</span>
          </div>
        </div>
      ))}
    </>
  );
}
