import { useState, useEffect } from "react";
import HeroSVG from "../svg/Hero";
import {Button, Link} from "@nextui-org/react";
import ArrowIcon from "../icons/ArrowIcon";

const features = [
    "Trade Logging",
    "AI Insights",
    "Real-Time News",
    "Seamless Setup"
]

export default function Hero() {
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const intervalDelayMilliseconds = features[index].length * 200;
    const interval = setInterval(() => {
      setIndex((prevIndex) => {
        // reset index if current index is greater than array size
        return prevIndex + 1 < features.length ? prevIndex + 1 : 0;
      });
    }, intervalDelayMilliseconds);

    return () => clearInterval(interval);
  });


    return (
        <div className="animate__animated animate__fadeInDown flex z-40 w-full h-auto items-center justify-center top-0 inset-x-0">
            <section id="hero" className="my-4 flex-col md:flex px-6 gap-4 w-full md:flex-row relative flex-nowrap items-center justify-between max-w-5xl">
                <section id="hero-left">
                    <div className="font-bold text-4xl">
                        <h1 className="text-green-500 animate__animated animate__fadeInUp" key={features[index]}>{features[index]}</h1>
                        <div className="flex gap-2">
                            <span>with</span>
                            <span className="underline font-extrabold decoration-purple-600 underline-offset-8">TradeVault</span>
                        </div>
                    </div>
                    <div className="my-2 py-2 text-gray-300">
                        <p>TradeVault provides advanced trade logging, AI-driven feedback for trades, real-time performance statistics, and the latest market news, all within a user-friendly interface.</p>
                    </div>
                    <div className="py-2">
                        <Button as={Link} color="success" variant="flat" href="/signup" className="text-white font-semibold">
                            Get Started
                            <ArrowIcon />
                        </Button> 
                    </div>
                </section>
                <section id="hero-right">
                    <HeroSVG />
                </section>
            </section>
        </div>
    )
}