import RobotIcon from "../icons/RobotIcon";
import NoteIcon from "../icons/NoteIcon";
import StockIcon from "../icons/StockIcon";
import { Chip } from "@nextui-org/react";

export default function Features() {
  return (
    <div className="animate__animated animate__fadeInDown flex z-40 w-full h-auto items-center justify-center top-0 inset-x-0">
      <section
        id="features"
        className="bg-gray-100 p-3 md:p-6 mx-3 rounded-2xl my-4 flex-col lg:flex gap-4 w-full lg:flex-row relative flex-nowrap items-center justify-between max-w-5xl"
      >
        <div class="lg:w-1/2 w-full mb-10 lg:mb-0 rounded-lg">
          <img
            alt="feature"
            class="object-cover object-center h-full w-full"
            src="/feature-photo.jpg"
          />
        </div>
        <div class="flex flex-col flex-wrap lg:py-6 lg:w-1/2 lg:pl-12 lg:text-left text-center">
          <div class="flex flex-col mb-10 lg:items-start items-center">
            <div class="w-12 h-12 inline-flex items-center justify-center rounded-full bg-green-100 text-green-500 mb-5">
              <RobotIcon />
            </div>
            <div class="flex-grow">
              <h2 class="text-gray-900 text-lg title-font font-medium mb-1">
                AI-Enhanced Trade Analysis
              </h2>
              <Chip
                className="mb-3"
                size="sm"
                radius="full"
                color="secondary"
                variant="flat"
              >
                Powered by OpenAI API
              </Chip>
              <p class="text-gray-900 leading-relaxed text-base">
                Leverage the power of OpenAI to get intelligent feedback on your
                trades. Our AI system analyzes market trends and your trade
                patterns, offering actionable insights to refine your strategies
                and decision-making process.
              </p>
            </div>
          </div>
          <div class="flex flex-col mb-10 lg:items-start items-center">
            <div class="w-12 h-12 inline-flex items-center justify-center rounded-full bg-blue-100 text-blue-500 mb-5">
              <NoteIcon />
            </div>
            <div class="flex-grow">
              <h2 class="text-gray-900 text-lg title-font font-medium mb-3">
                Trade and Investment Logging
              </h2>
              <p class="text-gray-900 leading-relaxed text-base">
                Maintain a detailed digital journal of your trading activities.
                Effortlessly log trades and investments, recording key details
                like dates, assets, quantities, and prices. TradeVault keeps
                your trading history organized, secure, and easily accessible.
              </p>
            </div>
          </div>
          <div class="flex flex-col mb-10 lg:items-start items-center">
            <div class="w-12 h-12 inline-flex items-center justify-center rounded-full bg-orange-100 text-orange-500 mb-5">
              <StockIcon />
            </div>
            <div class="flex-grow">
              <h2 class="text-gray-900 text-lg title-font font-medium mb-3">
                Real-Time Stock News
              </h2>
              <p class="text-gray-900 leading-relaxed text-base">
                Stay ahead in the market with real-time updates on stock news
                and trends. Our platform ensures you're constantly informed with
                the latest and most relevant market developments, aiding your
                trading decisions with timely, data-driven information.
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
